.edit-form {
    background-color: #eee;
    width: 200px;
    padding: 10px 15px;
}

.edit-form__fields .form-group {
    display: -ms-flexbox;
    display: flex;
    padding: 5px 0;
}

.edit-form__fields .form-group input, .edit-form__fields .form-group {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.edit-form__fields .form-group input {
    margin-left: 10px;
}

.edit-form__fields .form-group:nth-child(1) input {
    width: 50px;
}

.edit-form__fields .form-group:nth-child(2) input {
    width: 30px;
}

.edit-form__controls {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 20px;
}



