    margin: 0;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.templatesList-item__name h4 {
    color: #333;
    margin: 0;
    font-weight: normal;
}

.templatesList-item__name p {
    display: inline-block;
    -ms-flex-positive: 1;
        flex-grow: 1;
    margin: 5px 0 0 0;
    font-size: 12px;
    color: #999;
    text-align: right;
}

.templatesList-item__image {
    text-align: center;
    margin-top: 20px;
}

.templatesList-item__description {
